

:root {
  --primary-color: #ff4bba;
  --primary-color-dark: #bb3788;
  --secondary-color: rgb(0, 0, 0);
  --background-color: rgb(37, 28, 37);
  --text-color: #ff4bba;
  --box-shadow-color: #ff4bba;
  --box-shadow-color-dark: #ff4bba;
  --font-size: 16px;
  background-color: var(--background-color);
 
}


.itemcontainer {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
  gap: 20px;
  margin-top: 20px;
  
}

.miniitemdiv {
  text-align: center;
  width: 100px;
  padding: 5px;
  border: 0px solid var(--secondary-color);
  box-shadow: 0 0px 10px #000;
  background-color: var(--background-color);
  transition: all 0.3s ease;
}
// Ordeeeeeeeeeeeeeeeeeeeeeeeeer
.orderdiv {
  text-align: center;
  width: 145px;
  padding: 3px;
  box-shadow: 0 0px 10px #000;

  
}



.orderdiv img {
  
  max-height: 145px !important;
  height: auto;
  margin-top: 3px;
}



//end order _________________________________________________________________________



.itemdiv {
  text-align: center;
  width: 325px;
  padding: 20px;
  border: 0px solid var(--secondary-color);
  box-shadow: 0 0px 10px #000;
  background-color: var(--background-color);
  transition: all 0.3s ease;
}

.itemdiv:hover {
  transform: translateY(-5px);
  box-shadow: 0 0px 50px var(--box-shadow-color);
}

.itemdiv img {
  
  max-height: 300px ;
  height: auto;
  margin-top: 10px;
}

.itemdiv li {
  list-style: none;
  margin: 10px 0;
  font-size: var(--font-size);
}

.itemdiv button {
  background-color: var(--primary-color);
  // color: var(--background-color);
  border: none;
  padding: 8px 16px;
  font-size: var(--font-size);
  cursor: pointer;
  transition: all 0.3s ease;
  margin-right: 10px;
}

.itemdiv button:hover {
  background-color: var(--primary-color-dark);
}

button {
  background-color: var(--primary-color);
  // color: var(--background-color);
  border: none;
  padding: 8px 16px;
  font-size: var(--font-size);
  cursor: pointer;
  transition: all 0.3s ease;
  margin: 10px;
}

button:hover {
  background-color: var(--primary-color-dark);
}


form {
  display: flex;
  flex-direction: column;
  align-items: center;
  margin-top: 20px;
}

input[type="text"] {
  width: 100%;
  padding: 10px;
  margin: 10px 0;
  border: 1px solid var(--secondary-color);
  border-radius: 4px;
  box-sizing: border-box;
  font-size: var(--font-size);
  color: var(--text-color);  
  background-color: var(--background-color);
}

button[type="submit"] {
  background-color: var(--primary-color);
  border: none;
  padding: 10px 20px;
  font-size: var(--font-size);
  cursor: pointer;
  border-radius: 4px;
  transition: all 0.3s ease;
  margin-top: 10px;
}

button[type="submit"]:hover {
  background-color: var(--primary-color-dark);
}


header {
  display: flex;
  justify-content: space-between;
  align-items: center;
  background-color: var(--background-color);
  padding: 10px;
  
}

.svg {
  height: 50px;
  width: auto;
}

.navbar {
  display: flex;
  justify-content: flex-end;
  align-items: center;
}

.navbar a {
  color: var(--text-color);
}
h1{
  color: var(--text-color);
}

p{
  color: var(--text-color);  
}
